
  @import url('https://fonts.googleapis.com/css2?family=Poppins:ital@0;1&display=swap');


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    background-image: url("../img/back1.jpg");
   display: flex;
   justify-content: center;
   align-items: center;
   flex-wrap: wrap;
   height: 100vh;
   width: 100vw;
   font-family: 'Poppins', sans-serif;
}




.box{
    backdrop-filter: blur(15px);
    width: 30vw;
    height: 60vh;
    border-radius: 20px;
    background:rgba(165, 225,245, 0.4);
    overflow: hidden;
    transform: translate3d(0,0,0);
    min-width: 20rem;
    min-height: 25rem;
}





.inputData{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2rem 0 0 0;
}

.inputField{
    width: 60%;
    margin: auto ;
    border: none;
    outline: none;
    border-radius: 5rem;
    padding: 1rem;
    outline: none;
    font-size: 2rem;
}


.info{
     text-align: center;
     margin-top: 70px;
}

.location{
    text-align: center;
    margin-top: -30px;
    font-size: 37Px;
    font-weight: 800;
    text-transform: uppercase;
}

.ri-map-pin-line{
    font-size: 33px;
    margin-right: 10px;
    font-weight: 600;
}

.temp{
    font-size: 35Px;
    margin-left: 5px; margin-top: 20px;
    text-align: center;
    font-weight: 200;
}

.tempmin-max{
    font-size:25Px;
    color:red;
    margin-left: 10px;
    margin-top: 20px;
    text-align: center;
    font-weight: 200;
}

.errorMsg{
    text-align: center;
    margin-top: 120px;
    font-size: 20px;
}



.ri-temp-cold-fill{
    font-size: 33px;
}

.date , .time{
    margin-top: 20px;
    text-align: center;
    font-size: 28px;
}

